export const EPSILON = 0.00001;
export function round(value) {
    return Number.isInteger(value) ? value : Math.round(value * 100) / 100;
}
export function clamp(value, min = 0, max = 1) {
    return value <= min ? min : (value >= max ? max : value);
}
export function isCloseTo(a, b) {
    return Math.abs(a - b) <= EPSILON;
}
export function numberToString(value) {
    return Number.isInteger(value) ? value.toString() : value.toFixed(2);
}
export function isValidNumber(value) {
    return Number.isFinite(value) && !Number.isNaN(value);
}
export function parseNumber(value, fallback = 0) {
    const number = parseFloat(value);
    return isValidNumber(number) ? number : fallback;
}
const NUMBER_REGEX = /-?(?:\d\.?\d*[Ee][+\-]?\d+|(?:\d+\.\d*|\d*\.\d+)|\d+)/gm;
export function parseNumberList(value) {
    const list = [];
    let match;
    while (match = NUMBER_REGEX.exec(value)) {
        const number = parseFloat(match[0]);
        if (isValidNumber(number)) {
            list.push(number);
        }
    }
    return list;
}
export function numberListToString(value, separator = ' ') {
    return value.map(numberToString).join(separator);
}
