import { isCloseTo, RADIANS, SERIALIZE } from "../Utils";
export class Point {
    constructor(x, y) {
        this.x = x;
        this.y = y;
    }
    /**
     * Vector direction (angle)
     */
    get direction() {
        return Math.atan2(this.y, this.x);
    }
    get length() {
        return Math.sqrt(this.x * this.x + this.y * this.y);
    }
    get isZero() {
        return this.x === 0 && this.y === 0;
    }
    get isUnit() {
        return isCloseTo(this.length, 1);
    }
    isPerpendicularTo(vector) {
        return isCloseTo(this.dot(vector), 0);
    }
    distanceTo(other) {
        return Point.distance(this, other);
    }
    add(p) {
        return new Point(this.x + p.x, this.y + p.y);
    }
    sub(p) {
        return new Point(this.x - p.x, this.y - p.y);
    }
    scale(s) {
        return new Point(s * this.x, s * this.y);
    }
    offset(x, y) {
        return new Point(this.x + x, this.y + y);
    }
    /**
     * Dot product
     * @param vector
     */
    dot(vector) {
        return this.x * vector.x + this.y * vector.y;
    }
    /**
     * Cross product
     * @param vector
     */
    cross(vector) {
        return this.x * vector.y - this.y * vector.x;
    }
    negate() {
        return new Point(-this.x, -this.y);
    }
    toLength(length) {
        if (this.isZero) {
            return new Point(0, 0);
        }
        return this.scale(length / this.length);
    }
    /**
     * Normalize vector
     */
    toUnit() {
        return this.toLength(1);
    }
    equals(other) {
        return this.x === other.x && this.y === other.y;
    }
    clone() {
        return new Point(this.x, this.y);
    }
    [SERIALIZE]() {
        return [this.x, this.y];
    }
    static fromObject(o) {
        return new Point(o.x, o.y);
    }
    /**
     * Creates a vector from an angle
     * @param angle In degrees
     * @param length
     */
    static fromAngle(angle, length = 1) {
        return this.fromDirection(angle * RADIANS, length);
    }
    /**
     * Creates a vector from a direction
     * @param radians angle in radians
     * @param length
     */
    static fromDirection(radians, length = 1) {
        return new Point(length * Math.cos(radians), length * Math.sin(radians));
    }
    /**
     * Distance between two points
     * @param a
     * @param b
     */
    static distance(a, b) {
        return Math.sqrt((a.x - b.x) ** 2 + (a.y - b.y) ** 2);
    }
    /**
     * Center point
     * @param a
     * @param b
     */
    static center(a, b) {
        return new Point((a.x + b.x) / 2, (a.y + b.y) / 2);
    }
}
Point.ZERO = new Point(0, 0);
Point.ONE = new Point(1, 1);
